namespace Core {
    export interface ILogger {
        /**
         * 
         */
        d(...parts: any[]): void;

        /**
         * 
         */
        i(...parts: any[]): void;

        /**
         * 
         */
        w(...parts: any[]): void;

        /**
         * 
         */
        e(...parts: any[]): void;

        /**
         * 
         */
        l(level: ILogger.Level): ILogger;
    }

    export namespace ILogger {
        /**
         * 
         */
        export enum Level {
            /**
             * 
             */
            Debug,
            /**
             * 
             */
            Info,
            /**
             * 
             */
            Warn,
            /**
             * 
             */
            Error
        }
    }
}
