class E extends Error {
    public static SCHEMA_TAG_NOT_DECLARED: string = '';

    public static SCHEMA_CHILD_NOT_ALLOWED: string = '';

    public static LEX_ILLEGAL_SOURCE: string = '';

    public static LEX_UNEXPECTED_INDENTATION: string = '';

    public static TAG_PARAMS_TOO_FEW: string = '';

    public static TAG_PARAMS_TOO_MANY: string = '';

    public static TAG_CONTENT_FORBIDEN: string = '';

    public static TAG_CONTENT_REQUIRED: string = '';

    public static TAG_CHILDREN_TOO_FEW: string = '';

    public static TAG_CHILDREN_TOO_MANY: string = '';

    public static DEF_CHAR_AVATAR_NOT_FOUND: string = '';

    public static DEF_CHAR_POSES_NOT_FOUND: string = '';

    public static DEF_EPISODE_NOT_REGISTERED: string = '';

    public static DEF_EPISODE_NOT_BINDED: string = '';

    public static DEF_ROOM_EMPTY: string = '';

    public static DEF_MAP_REGION_BROKEN: string = '';

    public static DEF_MAP_BGIMAGE_NOT_FOUND: string = '';

    public static DEF_MAP_HLIMAGE_NOT_FOUND: string = '';

    public static DEF_MAP_REGION_NOT_FOUND: string = '';

    public static DEF_MAP_TARGET_NOT_FOUND: string = '';

    public static DEF_MAP_POINT_NOT_FOUND: string = '';

    public static SCENE_TYPE_UNKNOWN: string = '';

    public static ROOT_NOT_PARENT: string = '';

    public static ACT_ILLEGAL_POSITION: string = '';

    public static ACT_CHAR_NOT_ON: string = '';

    public static ACT_CHAR_ONSTAGE: string = '';

    public static ACT_ILLEGAL_STARS: string = '';

    public static ACT_CG_NOT_SHOWN: string = '';

    public static ACT_CG_ALREADY_SHOWN: string = '';

    public static ACT_ILLEGAL_OP: string = '';

    public static ACT_STATE_NOT_NUMERIC: string = '';

    public static ACT_DELTA_NOT_NUMERIC: string = '';

    public static ACT_OPTION_CAST_FAILURE: string = '';

    public static RES_INVALID_URI: string = '';

    public static ENV_NOT_AVAILABLE: string = '';

    public static EP_ENTITY_NOT_FOUND: string = '';

    public static EP_THEME_NOT_LOADED: string = '';

    public static G_PARENT_NOT_FOUND: string = '';

    /**
     * 
     */
    public signal: E.Signal;

    /**
     * 
     */
    constructor(message: string, lineNo?: number) {
        if ('captureStackTrace' in Error)
            Error['captureStackTrace'](this, E);
        this.signal = (lineNo in E.Signal) ?
            lineNo :
            E.Signal.OK;
        if (0 < lineNo)
            message = ' ' + lineNo + ' ' + message;
        super();
        this.name = 'BigineError';
        this.message = message;
    }
}

namespace E {
    export enum Signal {
        /**
         * 
         */
        BREAK = -99,
        /**
         * 
         */
        HALT,
        /**
         * 
         */
        OK = 0
    }
}
